import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import Multiselect from "multiselect-react-dropdown";
import avatar from "../../Assets/images/avatar.png";
import add from "../../Assets/images/add.png";
import MultiImageInput from "react-multiple-image-input";
import { useSelector, useDispatch } from "react-redux";
import { getAllCategory } from "../../Redux/Actions/CategoryAction";
import { getAllBrand } from "../../Redux/Actions/brandAction";
import { CompactPicker } from "react-color";
import { getOneCategory } from "../../Redux/Actions/SubCategoryAction";
import { craeteProduct } from "../../Redux/Actions/ProductAction";
import notify from "../../Hook/UseNotfications";
import { ToastContainer } from "react-toastify";
import AddProductHook from "../../Hook/Product/AddProductHook";
export default function AdminNewProduct() {
  const [
    images,
    setImages,
    ProdName,
    handleEnterName,
    handleEnterDesc,
    ProdDesc,
    PriceBefore,
    handleEnterPriceBefore,
    PriceAfter,
    handleEnterPriceAfter,
    Qty,
    handleEnterQantity,
    handleSelectedCategory,
    category,
    options,
    onSelect,
    onRemove,
    brand,
    Colors,
    removeColor,
    showColor,
    handleColorChange,
    handleSubmit,
    ShowColor,
    handleSelectedBrand,
  ] = AddProductHook();

  return (
    <>
      <div>
        <Row className="justify-content-start ">
          <div className="admin-content-text pb-4">   </div>
          <Col sm="8">
            <div className="text-form pb-2">  </div>
            <MultiImageInput
              images={images}
              setImages={setImages}
              theme={"light"}
              max={6}
              allowCrop={false}
            />
            <input
              type="text"
              className="input-form d-block mt-3 px-3"
              placeholder=" "
              value={ProdName}
              onChange={handleEnterName}
            />
            <textarea
              className="input-form-area p-2 mt-3"
              rows="4"
              cols="50"
              placeholder=" "
              onChange={handleEnterDesc}
              value={ProdDesc}
            />
            <input
              type="number"
              className="input-form d-block mt-3 px-3"
              placeholder="  "
              value={PriceBefore}
              onChange={handleEnterPriceBefore}
            />
            <input
              type="number"
              className="input-form d-block mt-3 px-3"
              placeholder=" "
              value={PriceAfter}
              onChange={handleEnterPriceAfter}
            />
            <input
              type="number"
              className="input-form d-block mt-3 px-3"
              placeholder=" "
              value={Qty}
              onChange={handleEnterQantity}
            />
            <select
              name="cat"
              className="select input-form-area mt-3 px-2 "
              onChange={handleSelectedCategory}
            >
              <option value="0"> </option>
              {category.data
                ? category.data.map((item) => {
                    return (
                      <option key={item._id} value={item._id}>
                        {item.name}
                      </option>
                    );
                  })
                : null}
            </select>
            <Multiselect
              className="mt-2 text-end"
              placeholder=" "
              options={options}
              onSelect={onSelect}
              onRemove={onRemove}
              displayValue="name"
              style={{ color: "red" }}
            />
            <select
              name="brand"
              id="brand"
              onChange={handleSelectedBrand}
              className="select input-form-area mt-3 px-2 "
            >
              <option value="val"></option>
              {brand.data
                ? brand.data.map((item) => {
                    return (
                      <option key={item._id} value={item._id}>
                        {item.name}
                      </option>
                    );
                  })
                : null}
            </select>
            <div className="text-form mt-3 ">   </div>
            <div className="mt-1 d-flex">
              {Colors.length >= 1
                ? Colors.map((color, index) => {
                    return (
                      <div
                        key={index}
                        onClick={() => removeColor(color)}
                        className="color ms-2 border  mt-1"
                        style={{ backgroundColor: color }}
                      ></div>
                    );
                  })
                : null}

              <img
                src={add}
                onClick={showColor}
                alt=""
                width="30px"
                height="35px"
                className=""
                style={{ cursor: "pointer" }}
              />
            </div>
            {/* <CirclePicker />
            CompactPicker
            <SketchPicker /> */}
            {ShowColor === true ? (
              <CompactPicker onChangeComplete={handleColorChange} />
            ) : null}
          </Col>
        </Row>
        <Row>
          <Col sm="8" className="d-flex justify-content-end ">
            <button onClick={handleSubmit} className="btn-save d-inline mt-2 ">
               
            </button>
          </Col>
        </Row>
        <ToastContainer />
      </div>
    </>
  );
}
