import React, { useState } from "react";
import { Col, Row } from "react-bootstrap";
import { useParams } from "react-router";
import ViewProductDetailsHook from "../../Hook/Product/ViewProductDetailsHook";
import AddToCartHook from "../../Hook/Cart/AddToCartHook";
import { ToastContainer } from "react-toastify";

export default function ProductTextDetails() {
  const { id } = useParams();
  const [item, images, cat, brand, products] = ViewProductDetailsHook(id);
  console.log(cat);
  console.log(item);
  console.log(brand);

  const [SelectedIndex, SelecyedColor, selectingColor, handleAddToCard] =
    AddToCartHook(id, item);

  return (
    <>
      <div>
        <Row className="mt-2">
          <div className="cat-text">{cat.name} :</div>
        </Row>
        <Row>
          <Col md="8">
            <div className="cat-title d-inline">
              {item.title}
              <div className="cat-rate d-inline mx-3">
                {item.ratingsAverage}
              </div>
            </div>
          </Col>
        </Row>
        <Row>
          <Col md="8" className="mt-4">
            <div className="cat-text d-inline"> : </div>
            <div className="barnd-text d-inline mx-1">{brand.name} </div>
          </Col>
        </Row>
        <Row>
          <Col md="8" className="mt-1 d-flex">
            {item.availableColors
              ? item.availableColors.map((color, index) => {
                  return (
                    <div
                      key={index}
                      onClick={() => selectingColor(index, color)}
                      className="color ms-2 "
                      style={{
                        backgroundColor: color,
                        border:
                          SelectedIndex === index ? "2px solid black" : "none",
                      }}
                    ></div>
                  );
                })
              : null}
          </Col>
        </Row>

        <Row className="mt-4">
          <div className="cat-text"> :</div>
        </Row>
        <Row className="mt-2">
          <Col md="10">
            <div className="product-description d-inline">
              {item.description}
            </div>
          </Col>
        </Row>
        <Row className="mt-4">
          <Col md="12">
            <div className="product-price d-inline px-3 py-3 border">
              {item.price} 
            </div>
            <div
              onClick={handleAddToCard}
              className="product-cart-add px-3 py-3 d-inline mx-3"
            >
               
            </div>
          </Col>
        </Row>

        <ToastContainer />
      </div>
    </>
  );
}
