import React from "react";
import { Col, Row } from "react-bootstrap";
import AddUserHook from "../../Hook/User/AddUserHook";
import { ToastContainer } from "react-toastify";

export default function UserAddAdress() {
  const [
    Home,
    AddressDetails,
    PhoneNum,
    handleEnterHome,
    handleEnterAddressDetails,
    handleEnterPhoneNum,
    sumbit,
  ] = AddUserHook();
  return (
    <>
      <div>
        <Row className="justify-content-start ">
          <div className="admin-content-text pb-2">  </div>
          <Col sm="8">
            <input
              type="text"
              value={Home}
              onChange={handleEnterHome}
              className="input-form d-block mt-3 px-3"
              placeholder="  ( - )"
            />
            <textarea
              className="input-form-area p-2 mt-3"
              value={AddressDetails}
              onChange={handleEnterAddressDetails}
              rows="4"
              cols="50"
              placeholder=" "
            />
            <input
              type="text"
              value={PhoneNum}
              onChange={handleEnterPhoneNum}
              className="input-form d-block mt-3 px-3"
              placeholder=" "
              maxLength={11}
            />
          </Col>
        </Row>
        <Row>
          <Col sm="8" className="d-flex justify-content-end ">
            <button onClick={sumbit} className="btn-save d-inline mt-2 ">
               
            </button>
          </Col>
        </Row>

        <ToastContainer />
      </div>
    </>
  );
}
