import React from "react";
import { Row } from "react-bootstrap";
import SideFilterSearchHook from "../../Hook/Search/SideFilterSearchHook";

export default function SideFilter() {
  const [
    category,
    brand,
    handleClickCategory,
    handleClickBrand,
    handlePriceFrom,
    handlePriceTo,
  ] = SideFilterSearchHook();
  let localFrom = localStorage.getItem("priceFrom");
  let localTo = localStorage.getItem("priceTo");
  return (
    <>
      <div className="mt-3">
        <Row>
          <div className="d-flex flex-column mt-2">
            <div className="filter-title"></div>
            <div className="d-flex mt-3">
              <input onChange={handleClickCategory} type="checkbox" value="0" />
              <div className="filter-sub me-2 "></div>
            </div>
            {category ? (
              category.map((item, index) => {
                return (
                  <div key={index} className="d-flex mt-2">
                    <input
                      onChange={handleClickCategory}
                      type="checkbox"
                      value={item._id}
                    />
                    <div className="filter-sub me-2 ">{item.name}</div>
                  </div>
                );
              })
            ) : (
              <h6> </h6>
            )}
          </div>

          <div className="d-flex flex-column mt-2">
            <div className="filter-title mt-3"></div>
            <div className="d-flex mt-3">
              <input onChange={handleClickBrand} type="checkbox" value="0" />
              <div className="filter-sub me-2 "></div>
            </div>

            {brand ? (
              brand.map((item, index) => {
                return (
                  <div key={index} className="d-flex mt-2">
                    <input
                      onChange={handleClickBrand}
                      type="checkbox"
                      value={item._id}
                    />
                    <div className="filter-sub me-2 ">{item.name}</div>
                  </div>
                );
              })
            ) : (
              <h6>  </h6>
            )}
          </div>

          <div className="filter-title my-3"></div>
          <div className="d-flex">
            <p className="filter-sub my-2">:</p>
            <input
              value={localFrom}
              onChange={handlePriceFrom}
              className="m-2 text-center"
              type="number"
              style={{ width: "50px", height: "25px" }}
            />
          </div>
          <div className="d-flex">
            <p className="filter-sub my-2">:</p>
            <input
              value={localTo}
              onChange={handlePriceTo}
              className="m-2 text-center"
              type="number"
              style={{ width: "50px", height: "25px" }}
            />
          </div>
        </Row>
      </div>
    </>
  );
}
