import React from "react";
import { Col, Row } from "react-bootstrap";
import EditAddressHook from "../../Hook/User/EditAddressHook";
import { useParams } from "react-router";
import { ToastContainer } from "react-toastify";

export default function UserEditAdress() {
  const id = useParams();
  console.log(id);
  const [
    NewHome,
    NEWHomeDetails,
    NEWPhone,
    handaleEnterNewHome,
    handaleEnterNewDetails,
    handaleEnterNewPhone,
    onSumbit,
  ] = EditAddressHook(id);
  return (
    <>
      <div>
        <Row className="justify-content-start ">
          <div className="admin-content-text pb-2">  </div>
          <Col sm="8">
            <input
              type="text"
              className="input-form d-block mt-3 px-3"
              value={NewHome}
              onChange={handaleEnterNewHome}
              placeholder="  ( - )"
            />
            <textarea
              className="input-form-area p-2 mt-3"
              rows="4"
              cols="50"
              value={NEWHomeDetails}
              onChange={handaleEnterNewDetails}
              placeholder=" "
            />
            <input
              type="text"
              value={NEWPhone}
              onChange={handaleEnterNewPhone}
              className="input-form d-block mt-3 px-3"
              placeholder=" "
            />
          </Col>
        </Row>
        <Row>
          <Col sm="8" className="d-flex justify-content-end ">
            <button onClick={onSumbit} className="btn-save d-inline mt-2 ">
                
            </button>
          </Col>
        </Row>
      </div>

      <ToastContainer />
    </>
  );
}
